import React, {useRef, useState} from "react"
import styled from "styled-components"
import Button from "../components/Button"
import {observer} from "mobx-react"
import useStores from "../store"
import {useHistory} from 'react-router-dom'

const Wrapper = styled.div`
background-color: #fff;
padding: 40px;
margin-left: auto;
margin-right: auto;
width:460px;
display: flex;
flex-direction: column;
align-items: center;
border: 1px dashed #ccc;
p{
text-align: center;
font-size: 18px;
font-weight: bold;
}
label{
display: flex;
margin-bottom: 16px;
justify-content: center;
align-items: center;

span{
display: inline-block;
font-size: 12px;
width: 6em;
margin-right: 20px;
}
input{
padding: 4px;
border: 1px solid #ccc;
border-radius: 4px;
&:focus{
border: 1px solid dodgerblue;
}
}
}
`;
const Login = observer(() => {
    const {AuthStore} = useStores();
    const [username, setUsername] = useState('')
    const [password, setPassword] = useState('')
    const refForm = useRef(null)
    const refName = useRef(null)
    const refPassword = useRef(null)
    const history = useHistory();
    const submit = () => {
        AuthStore.setUsername(username)
        AuthStore.setPassword(password)
        AuthStore.login().then(
            () => {
                history.push('/')
            },
            () => {
                console.log('登录失败')
            }
        )
        console.log(refForm)
    }
    const changePassword = () => {
        if (refPassword.current !== null) {
            const value = refPassword.current.value.replace(' ', '');
            refPassword.current.value = value;
            setPassword(value)
        }

    }
    const changeName = () => {
        if (refName.current !== null) {
            const value = refName.current.value.replace(' ', '');
            refName.current.value = value
            setUsername(value)
        }
    }
    const enterLog = (event) => {
        if (event.key === 'Enter') {
            submit()
        }
    }

    return (
        <Wrapper>
            <p>登录</p>
            <p></p>
            <form >
                <label name='username'>
                    <span>用户名:</span>
                    <input
                        type='text' defaultValue={username}
                        ref={refName}
                        onBlur={changeName}
                    />
                </label>

                <label name='password'>
                    <span>密码:</span>
                    <input type='password' defaultValue={password}
                           ref={refPassword}
                           onBlur={changePassword}
                    />
                </label>
            </form>
            <input type="submit" onClick={submit} value='登录'/>
        </Wrapper>
    )
})
export default Login;